.forecast-container {
  background: linear-gradient(to bottom right, #e1e1e1, #73caec);
  border: 8px solid;
  border-radius: 10px;
  padding: 20px;
  border-image: repeating-linear-gradient(to bottom right, #33475b,#0033cc,#ff77cc,rgb(255,122,89)) 20;
  text-align: center;
  max-width: 400px;
  margin: auto;
}
  
  .city-name {
    font-size: 2em;
    font-weight: bold;
    color: #2c3e50;
    margin-bottom: 20px;
  }
  
  .weather-info {
    display: flex;
    flex-direction: column;
    align-items: center;

  }
  
  .temp-box {
    background: #c5f14a;
    border-radius: 10px;
    padding: 10px;
    box-shadow: inset 5px 5px 10px #8c8c8c, inset -5px -5px 10px #dcdcdc;
  }
  
  .temp-box h2 {
    font-size: 3em;
    margin: 0;
    color: #fff;
  }
  
  .temp-box p {
    font-size: 1.2em;
    text-transform: capitalize;
    color: #fff;
  }
  
  .other-details {
    background: #7d7d7d;
    border-radius: 10px;
    padding: 15px;
    width: 100%;
    box-shadow: inset 5px 5px 10px #5c5c5c, inset -5px -5px 10px #9e9e9e;
  }
  
  .other-details p {
    font-size: 1em;
    color: #ffffff;
    margin: 5px 0;
  }

 
  
  h2 {
    color: #333;
  }
  
  p {
    font-size: 1.2em;
    color: #555;
  }
  .developer-info {
    background-color: rgb(200, 223, 0);
    padding: 5px;
    text-align: center;
    font-size: 12px;
    font-weight: bold;
    border-radius: 15px 50px;
    }
  
  